.page {
  height: 100%;
}
.page .page__main-list {
  display: none;
  margin: 0;
}
@media (min-width: 1440px) {
  .page .page__main-list {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
  }
}
.page__main-list .page-list__link {
  color: #ffffff;
}
.page__body {
  display: flex;
  flex-direction: column;
  background-color: #e6e8f1;
  font-family: "Blogger Sans", sans-serif;
  font-size: 16px;
  line-height: 18px;
  margin: 0 auto;
}
.page__body--catalog {
  background-color: #e5e5e5;
}
.page__menu-wrapper {
  display: flex;
}
@media (min-width: 768px) {
  .page__menu-wrapper {
    order: -2;
  }
}
@media (min-width: 1440px) {
  .page__menu-wrapper {
    order: -3;
  }
}
.page__title {
  font-weight: 700;
  font-size: 36px;
  line-height: 36px;
  color: #ffffff;
  margin: 0;
  width: max-content;
  position: relative;
}
.page__title::after {
  content: "";
  position: absolute;
  right: 0;
  bottom: -13px;
  left: 0;
  height: 5px;
  border-radius: 4px;
  background: #ff8d30;
  z-index: 3;
}
@media (min-width: 768px) {
  .page__title {
    font-size: 60px;
    line-height: 1em;
  }
  .page__title::after {
    bottom: -70px;
    height: 8px;
  }
}
.page__main-logo {
  background: url("../img/logo-light.svg") no-repeat;
  width: 96px;
  height: 15px;
}
@media (min-width: 768px) {
  .page__main-logo {
    background: url("../img/logo-full-light.svg") no-repeat;
    width: 166px;
    height: 42px;
  }
}
@media (min-width: 1440px) {
  .page__main-toggle {
    display: none;
  }
}
.main-header {
  background-color: #182044;
  border-radius: 0 0 30px 30px;
  z-index: 6;
}
@media (min-width: 768px) {
  .main-header {
    border-radius: 0 0 60px 60px;
    background-color: #182044;
  }
}
@media (min-width: 1440px) {
  .main-header {
    display: grid;
  }
}
.main-header__wrapper {
  padding: 96px 25px 11px;
}
@media (min-width: 768px) {
  .main-header__wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 225px 75px 65px;
  }
}
@media (min-width: 1440px) {
  .main-header__wrapper {
    grid-template-columns: min-content;
    padding: 225px 124px 65px;
  }
}
.main-header__wrapper .main-header__contacts {
  display: none;
}
@media (min-width: 768px) {
  .main-header__wrapper .main-header__contacts {
    display: flex;
  }
}
.main-header .main-header__list {
  display: none;
}
@media (min-width: 1440px) {
  .main-header .main-header__list {
    display: flex;
    justify-content: space-between;
  }
}
@media (min-width: 1440px) {
  .main-header__list .page-list__link {
    color: #ffffff;
  }
}
.main-header__logo {
  width: min-content;
}
@media (min-width: 1440px) {
  .main-header__logo {
    order: -2;
    height: 50px;
  }
}
.main-header__logo--active {
  width: min-content;
  background: url("../img/logo-dark.svg") no-repeat;
}
@media (min-width: 768px) {
  .main-header__logo--active {
    background: url("../img/logo-full-dark.svg") no-repeat;
  }
}
.main-header__return-button {
  display: none;
  font-weight: 700;
  font-size: 20px;
  line-height: 20px;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  justify-self: end;
  align-self: center;
}
.main-header__return-button::after {
  display: block;
  content: "";
  width: 32px;
  height: 16px;
  margin-left: 28px;
  background: url("../img/icon-arrow-round.svg") no-repeat center;
}
@media (min-width: 768px) {
  .main-header__return-button {
    display: flex;
    order: 1;
  }
}
@media (min-width: 1440px) {
  .main-header__return-button {
    grid-column: 4 / 5;
  }
}
.page-list {
  list-style-type: none;
  display: flex;
  flex-direction: column;
  padding: 0;
  align-items: center;
  grid-column: 2 / -2;
}
@media (min-width: 768px) {
  .page-list {
    align-items: start;
  }
}
@media (min-width: 1440px) {
  .page-list {
    order: -2;
    flex-direction: row;
  }
  .page-list::before {
    display: block;
    content: "";
    width: 60px;
    height: 2px;
    background-color: #ffffff;
    margin-right: 45px;
  }
}
@media (min-width: 768px) {
  .page-list__item:not(:last-child) {
    margin-right: 33px;
  }
}
.page-list__link {
  color: #192144;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 30px;
  line-height: 35px;
  font-weight: 500;
}
@media (min-width: 768px) {
  .page-list__link {
    font-size: 60px;
    line-height: 71px;
  }
}
@media (min-width: 1440px) {
  .page-list__link {
    color: #ffffff;
    font-size: 20px;
    line-height: 20px;
  }
}
.main-container {
  flex-grow: 1;
}
.contacts-list {
  padding: 0;
  margin: 0 0 30px;
  list-style-type: none;
}
@media (min-width: 1440px) {
  .contacts-list {
    display: flex;
  }
}
.contacts-list__item {
  font-size: 16px;
  line-height: 18px;
  font-weight: 700;
  text-transform: uppercase;
  display: grid;
  grid-template-columns: auto 1fr;
  column-gap: 16px;
  align-items: center;
}
@media (min-width: 768px) {
  .contacts-list__item {
    font-size: 28px;
    line-height: 1em;
    column-gap: 21px;
  }
}
@media (min-width: 1440px) {
  .contacts-list__item {
    column-gap: 0;
  }
}
.contacts-list__link {
  display: flex;
  align-items: center;
  border-radius: 50%;
  width: 45px;
  height: 45px;
  color: #192144;
  text-decoration: none;
  background-color: #121936;
}
@media (min-width: 768px) {
  .contacts-list__link {
    width: 68px;
    height: 68px;
  }
}
@media (min-width: 1440px) {
  .contacts-list__link {
