@media (min-width: 1440px) {
  .contacts-list__phone::after,
  .contacts-list__email::after {
    content: "";
  }
}
.contacts-list__text {
  color: #ffd74b;
  margin: 0;
}
.contacts-list__text--dark {
  color: #192144;
}
@media (min-width: 1440px) {
  .contacts-list__text--dark {
    display: none;
  }
}
.contacts-list__link--registration {
  font-weight: 700;
  line-height: 22px;
  display: flex;
  align-items: center;
}
@media (min-width: 1440px) {
  .contacts-list__link--registration {
    margin-right: 31px;
    min-width: 90px;
    height: 90px;
  }
}
.social-media {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  max-width: 100%;
}
.social-media__item {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 45px;
  height: 45px;
}
@media (min-width: 768px) {
  .social-media__item {
    width: 62px;
    height: 62px;
  }
}
.social-media__link {
  background-repeat: no-repeat;
  background-position: center;
}
.social-media__link--telegram {
  background-image: url("../img/logo-telegram.svg");
}
.social-media__link--vkontakte {
  background-image: url("../img/logo-vkontakte.svg");
}
.social-media__link--youtube {
  background-image: url("../img/logo-youtube.svg");
}
.social-media__footer-link {
  background-color: #ffd74b;
  display: block;
  width: 45px;
  height: 45px;
  border-radius: 50%;
}
@media (min-width: 768px) {
  .social-media__footer-link {
    min-width: 62px;
    height: 62px;
  }
}
.main-nav {
  width: calc(100vw - 50px);
  display: grid;
  grid-template-columns: 1fr;
  background-color: #182044;
  padding: 19px 25px 27px;
  position: absolute;
  z-index: 4;
}
@media (min-width: 768px) {
  .main-nav {
    width: calc(100vw - 80px);
    background-color: #182044;
    padding: 45px 40px;
  }
}
@media (min-width: 1440px) {
  .main-nav {
    width: calc(100vw - 120px);
    grid-template-columns: min-content 1fr 1fr 1fr min-content;
    padding: 47px 60px 0;
  }
}
.main-nav--main-page {
  background-color: #161d36;
}
@media (min-width: 768px) {
  .main-nav--main-page {
    background-color: #182044;
  }
}
@media (min-width: 1440px) {
  .main-nav--main-page {
    background-color: #161d36;
  }
}
.main-nav--active {
  background-color: #ffffff;
}
.main-nav__wrapper {
  display: flex;
  flex-direction: column;
}
.main-nav__list {
  margin: 78px 0 0 0;
}
@media (min-width: 768px) {
  .main-nav__list {
    grid-column: -1 / 1;
    margin: 98px 57px 144px;
  }
}
@media (min-width: 1440px) {
  .main-nav__list {
    grid-column: 2 / -3;
    flex-direction: row;
    align-items: center;
    margin: 0 0 0 38px;
  }
}
@media (min-width: 1440px) {
  .main-nav .contacts-list {
    order: -1;
    grid-column: -2 / -3;
    justify-content: end;
    margin: 0;
  }
}
.main-nav .main-nav__login {
  padding: 12px 0 8px;
  margin: 52px 0;
  line-height: 30px;
}
@media (min-width: 768px) {
  .main-nav .main-nav__login {
    position: absolute;
    top: 40px;
    right: 40px;
    padding: 16px 28px 14px 29px;
    margin: 0;
    font-size: 20px;
    line-height: 1em;
    min-width: 131px;
  }
}
@media (min-width: 1440px) {
  .main-nav .main-nav__login {
    position: static;
    order: 0;
    grid-column: -1 / -2;
    font-size: 20px;
    line-height: 20px;
  }
}
@media (min-width: 768px) {
  .main-nav .main-nav__login--active {
    font-weight: 400;
  }
}
@media (min-width: 768px) {
  .main-nav__contacts {
    justify-content: start;
    grid-column: -1 / 1;
    margin: 0 0 122px 57px;
  }
}
@media (min-width: 1440px) {
  .main-nav__contacts {
    display: none;
  }
}
.main-nav__contacts .social-media__item:not(:last-child) {
  margin-right: 27px;
}
.main-nav__toggle {
  position: absolute;
  top: 23px;
  right: 25px;
}
.main-nav__toggle::before,
.main-nav__toggle::after {
  display: block;
  content: "";
  width: 25px;
  height: 3px;
  border-radius: 1.5px;
  background-color: #ffffff;
}
.main-nav__toggle::after {
  margin-top: 5px;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    top: 60px;
    right: calc(50% - 13px);
  }
}
@media (min-width: 1440px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav__toggle--active::before,
.main-nav__toggle--active::after {
  background-color: #cccccc;
}
.main-nav__toggle--active::before {
  transform: rotate(45deg);
}
.main-nav__toggle--active::after {
  margin-top: -3px;
  transform: rotate(-45deg);
}
@media (min-width: 768px) {
  .main-nav__socials {
    justify-content: start;
    margin: 0 0 0 65px;
  }
}
.main-nav--nojs {
  background: #ffffff;
}
.options {
  background-color: #edeff6;
  padding: 71px 25px 34px;
  border-radius: 0 0 30px 30px;
  position: relative;
  z-index: 5;
}
.options::before {
  display: block;
  content: "";
  width: 100%;
  height: 50px;
  position: absolute;
  background-color: #edeff6;
  top: -50px;
  left: 0;
  z-index: -1;
}
@media (min-width: 768px) {
  .options {
    padding: 73px 75px 80px;
    border-radius: 0 0 60px 60px;
    background: url("../img/background-square--tablet.svg") no-repeat top 406px left 40px, url("../img/background-flags--tablet.png") no-repeat top 80px right 40px #edeff6;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .options {
    background: url("../img/background-square--tablet.svg") no-repeat top 406px left 40px, url("../img/background-flags--tablet@2x.png") no-repeat top 80px right 40px #edeff6;
    background-size: 141px 141px, 288px 574px;
  }
}
@media (min-width: 1440px) {
  .options {
    padding: 161px 200px 138px 130px;
    display: grid;
    column-gap: 171px;
    grid-template-columns: 560px 1fr;
    grid-template-rows: min-content min-content min-content;
    background: url("../img/background-square--tablet.svg") no-repeat top 90px left 60px, url("../img/background-flags--desktop.png") no-repeat top 88px right 60px #edeff6;
  }
}
@media (min-width: 1440px) and (min-resolution: 2dppx) {
  .options {
    background: url("../img/background-square--tablet.svg") no-repeat top 90px left 60px, url("../img/background-flags--tablet@2x.png") no-repeat top 88px right 60px #edeff6;
    background-size: 140px 140px, 288px 574px;
  }
}
